export const validation = {
    validatePassword,
    validateSuccess,
    validateNotBlank,
    validateUsername,
    validateName,
    validateSurname,
    validateEmail,
    validatePhoneNumber,
    validateDescription,
    validateNumberGreater
};

function validatePhoneNumber (phoneNumber) {
    const PHONEPL_REGEX = RegExp(
      "(?:1[2-8]|2[2-69]|3[2-49]|4[1-68]|5[0-9]|6[0-35-9]|[7-8][1-9]|9[145])\\d{7}"
    );
  
    if (!PHONEPL_REGEX.test(phoneNumber)) {
      return {
        validateStatus: "error",
        errorMsg: "Niepoprawny format numeru telefonu!",
      };
    } else if (phoneNumber.length > 30) {
      return {
        validateStatus: "error",
        errorMsg: `Dugo numer telefonu jest zbyt duga - moe mie maksymalnie 30 znakw!`,
      };
    } else {
      return {
        validateStatus: "success",
        errorMsg: null,
      };
    }
  }

function validateDescription(description) {
    if (description.length > 255) {
        return {
            validateStatus: "error",
            errorMsg: `Opis jest zbyt dugi - moe mie maksymalnie 255 znakw!`
        };
    } else {
        return {
            validateStatus: "success",
            errorMsg: null,
        };
    }
}

function validateNumberGreater(number) {
    if (number < 0) {
        return {
            validateStatus: "error",
            errorMsg: `Wprowadzona warto nie jest liczb wiksz lub rwn 0!`
        };
    } else {
        return {
            validateStatus: "success",
            errorMsg: null,
        };
    }
}

function validateSurname(surname) {
    if (surname.length > 60) {
        return {
            validateStatus: "error",
            errorMsg: `Nazwisko jest zbyt dugie - moe mie maksymalnie 60 znakw!`
        };
    } else {
        return {
            validateStatus: "success",
            errorMsg: null,
        };
    }
}

function validateName(name) {
    if (name.length > 60) {
        return {
            validateStatus: "error",
            errorMsg: `Imi jest zbyt dugie - moe mie maksymalnie 60 znakw!`
        };
    } else {
        return {
            validateStatus: "success",
            errorMsg: null,
        };
    }
}

function validateUsername(username) {
    if (username.length < 7) {
        return {
            validateStatus: "error",
            errorMsg: `Nazwa uytkownika jest zbyt krtka, wymagane jest 7 znakw!`
        };
    } else if (username.length > 60) {
        return {
            validateStatus: "error",
            errorMsg: `Nazwa uytkownika jest zbyt duga - moe mie maksymalnie 60 znakw!`
        };
    } else {
        return {
            validateStatus: "success",
            errorMsg: null,
        };
    }
}

function validateEmail (email) {
    if (!email) {
      return {
        validateStatus: "error",
        errorMsg: "Email nie moe by pusty!"
      };
    }
  
    const EMAIL_REGEX = RegExp(
      /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
    if (!EMAIL_REGEX.test(email)) {
      return {
        validateStatus: "error",
        errorMsg: "Niepoprawny format emaila!"
      };
    }
  
    if (email.length > 60) {
      return {
        validateStatus: "error",
        errorMsg: `Email jest zbyt dugi - moe mie maksymalnie 60 znakw!`
      };
    }
  
    return {
      validateStatus: "success",
      errorMsg: null
    };
}

function validatePassword(password) {
    const pattern = new RegExp("[0-9]");

    if (password.length < 7) {
        return {
            validateStatus: "error",
            errorMsg: `Haso jest zbyt krtkie, wymagane jest 7 znakw!`
        };
    } else if (!pattern.test(password)) {
        return {
            validateStatus: "error",
            errorMsg: `Haso powinno zawiera co najmniej jedn cyfr!`
        };
    } else {
        return {
            validateStatus: "success",
            errorMsg: null
        };
    }
}

function validateNotBlank(input) {
    if (input.length < 1) {
        return {
            validateStatus: "error",
            errorMsg: `Pole nie powinno by puste!`
        };
    } else {
        return {
        validateStatus: "success",
        errorMsg: null
      };
    }
}

function validateSuccess(request) {
    return {
        validateStatus: "success",
        errorMsg: null
      };
}